.news {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.news img {
  width: 100%;
}

.news a {
  color: black;
  cursor: pointer;
  padding: 6px 6px;
}

.news a:hover {
  background-color: #131d9a;
  color: #fff;
  padding: 6px 6px;
}

.news-section-left, .news-section-right {
  padding-bottom: 20px;
  width: 100%;
}

.news-section-right img {
  -ms-flex-align: end;
      align-items: flex-end;
}

.created-date {
  font-size: 12px;
  font-style: italic;
}

.event-address {
  list-style-type: none;
}

@media (min-width: 600px) {
  .news-section-left, .news-section-right {
    width: 50%
  }

  .news-section-left {
    padding-right: 10px;
  }

  .news-section-right {
    padding-left: 10px;
  }
}



